<template>
    <div>
        <v-head></v-head>
        <div class="clear"></div>
        <div class="selectBar" >
          <div id="selectForm">
              <el-form  id="select">
              <div >
              <el-form-item label="课程性质:" class="labels">
              <el-select v-model="typeSelect" placeholder="请选择" class="sel1">
                    <el-option  v-for="item in leixing"  :key="item.value":label="item.label"  :value="item.value">   </el-option>
                </el-select>
              </el-form-item>
              </div>
              <div>
              <el-form-item label="有无余量:" class="labels">
              <el-select v-model="yuliang" placeholder="请选择" class="sel2">
                    <el-option  v-for="item in youwu"  :key="item.value"  :label="item.label"  :value="item.value"> </el-option>
                </el-select>
              </el-form-item>
              <el-button type="primary" @click="search" id="search">查询</el-button>
              </div>
              <div class="clear"> </div>
            </el-form>
          </div>
        </div>
        <div class="tableBar" ref="tableBar">
        <div id="table">
          <el-table
                :data="tableData3"
                id="classTable"
                border>
                <el-table-column
                  
                  prop="cID"
                  label="课程编号"
                  >
                </el-table-column>
                <el-table-column
                  prop="cname"
                  label="课程名字"
               >
                </el-table-column>
                <el-table-column
                  prop="ctype"
                  label="课程性质"
                  >
                </el-table-column>
                <el-table-column
                  prop="classroom"
                  label="上课地点"
                 >
                </el-table-column>
                <el-table-column
                  prop="weeks"
                  label="上课时间"
                  >
                </el-table-column>
                <el-table-column
                  prop="cbelongto"
                  label="开课学院"
                 >
                </el-table-column>
			
                <el-table-column
                  prop="teacher"
                  label="开课教师"
                 >
                </el-table-column>
			
                <el-table-column
                  prop="num"
                  label="课程余量"
                 >
                </el-table-column>
				 <el-table-column
                  label="选否">
		        <template slot-scope="scope">
			      <input v-model="scope.row.selected" type="checkbox">
            <label >选择</label>
              </template>
          </el-table-column>				
              </el-table>
        
        </div>
	    <br/>
		<el-button type="primary" @click="submit()">提交</el-button>
      </div>
    </div>
</template>

<script>
import vHead from '../commons/StudentNav.vue';

export default {
      created(){
        var vm=this;
        $.ajax({
          type:'post',
          dataType:'json',
          url:'/sutdent/department',
          data:{
            "id":localStorage.getItem("stuId")
          },
          success:function(response){
            console.log(response)
            console.log(response.depId)
            vm.depId=response.depId;
          },error:function(err){
            console.log(err);
            console.log("error");
          }
        })

      },
      data() {
      return {
        depId:'',
        typeSelect: '',
        yuliang:'',
        leixing:[
           {label:"专业选修课",value:2},
           {label:"通选课",value:3},           
        ],
        youwu:[
          {label:"全部",value:0}, 
          {label:"有",value:1},
          {label:"无",value:2}
        ],
        tableData3: [
         ],
        tableData4:[

        ]

      }
    },
    methods:{
      changSelected(row){
        console.log("->",row.selected)
      },
      search(){
        
        console.log(this.yuliang,this.typeSelect);
        console.log(this.depId);
        this.tableData3=[];
        if(this.typeSelect==''){
          this.$message({
            type:'info',
            message:"选课类型不能为空"
          })
        }else if(this.yuliang!=1&&this.yuliang!=2&&this.yuliang!=0){
          this.$message({
            type:'info',
            message:"选择余量设置"
          })
        }
        else{
          var vm=this;
          $.ajax({
            type:'post',
            dataType:'json',
            url:'/student/availableCourse',
            data:{
              "depId":this.depId,
              "type":this.typeSelect,
              "stuId":localStorage.getItem("stuId"),
              "yuliang":this.yuliang,

            },
            success:function(response){
              if(response.string!=""){
                vm.analysisData(vm,response.string)
                vm.$refs.tableBar.style.display="block";
              }else{
                vm.$message({
                  type:'info',
                  message:'暂无此类型选课'
                })
              }
            },
            error:function(err){
              console.log(err);
              console.log("error");
            }
          })}
      },
       analysisData(vm,coursesList){
          var arraya=coursesList.split(';');
          console.log(arraya.length);
          for(var i=0;i<arraya.length;i++){
             var ele=arraya[i].split(',');
             var dist=new Array();
            for(var j=0;j<ele.length;j++){
                var eleItem=ele[j].split(':')
                console.log(eleItem[0],'->',eleItem[1])
                if(eleItem[0]!='selected')
                  dist[eleItem[0]]=eleItem[1]
                else
                  dist[eleItem[0]]=Boolean(parseInt(eleItem[1]))
            }

          vm.tableData3.push(dist);
        }
        console.log(vm.tableData3)
      },
      submit(){
        var ss=[];
        for(var i=0;i<this.tableData3.length;i++){
           console.log(this.tableData3[i].selected)
        }


        // var vm=this;
        // $.ajax({
        //   type:'post',
        //   dataType:'json',
        //   url:'/student/submitSelect',
        //   data:{
        //     "selectCourse":,
        //   },
        //   success:function(response){

        //   },
        //   error:function(err){
        //     console.log(err);
        //   }
        // })
      }
    },

    components:{
        vHead,
    }
}

</script>
 <style scoped>
 .clear{
     clear:both;
 }
.selectBar{
    margin-top:4px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    /* height:80px; */
    padding:10px;
}
.tableBar{
    margin-top:5px;
    padding-top:6px;
    background-color:#eef1f6;
    color: #333;
    text-align: center;
    /* height:500px; */
    padding:10px;
    /* display:none; */
}
#selectForm{

    padding-top: 1px;

}
#select{
    width:40%;
    margin:0px auto;
}
.labels{
    float:left;
    width:40%;
    display: inline;
}
#search{
  margin-top:36px;
  margin-left:10px;
  float:left;
}
#table{
  width:90%;
  text-align:left;
  margin:0px auto;
}
#classTable{
  width:100%;
}

</style>


